.popover {
  display: none;
  position: absolute;
  top: calc(100% + 7px);
  left: 50%;
  transform: translate(-54%);
  background-color: hsla(224, 60%, 16%, 1);
  min-height: 40px;
  text-align: center;
  color: $white;
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  padding: 14px;
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  min-width: 60px;
  z-index: 100;
  border-radius: 4px;

  &::after {
    position: absolute;
    top: -5px;
    border-top: 10px solid hsla(224, 60%, 16%, 1);
    border-left: 10px solid transparent;
    transform: rotate(-45deg);
    content: "";
  }
}

.popover__desc {
  margin: 0;
  width: max-content;
}

.list-way:hover + .popover {
  display: block;
}

.list-way:hover .popover {
  display: block;
}

.card__item-transport-container:hover + .popover {
  display: block;
}

.popover--flag {
  min-width: fit-content;
}

.form-step-second__img-container:hover {
  position: relative;
}

.form-step-second__img-container:hover .popover {
  display: block;
}

.card__flag-wrapper {
  position: relative;
}

.card__flag-wrapper:hover .popover {
  display: none;
}

.card-country__flag-wrapper:hover .popover {
  display: none;
}

.companion-card__flag-wrapper {
  position: relative;
}

.companion-card__flag-wrapper:hover .popover {
  display: block;
}

.companion-card__item-way:hover .popover {
  display: block;
}

.step__third-img-container:hover .popover {
  display: block;
}


@media (min-width: $tablet-width) {
  .popover {
    font-size: 20px;
    padding: 20px;
    min-width: 110px;
    min-height: 60px;
  }

  .popover--plane {
    padding-left: 20px;
    padding-right: 20px;
  }

  .popover--bus {
    padding-left: 25px;
    padding-right: 25px;
  }

  .popover--bike {
    padding-left: 20px;
    padding-right: 21px;
  }

  .popover--run {
    padding-left: 22px;
    padding-right: 26px;
  }
}
